@use '../util' as *;

header {
    background-color: white;
    height: rem(65);
    display: flex;
    justify-content: center;
    align-items: center;
}

.main-header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: rem(65);
}

.right-side {
    display: flex;
    align-items: center;
    margin-right: rem(90);
    gap: rem(16);

    img {
        border-radius: 50%;
        width: rem(40);
        height: rem(40);
        object-fit: cover;
    }
}

.left-side {
    display: flex;
    align-items: center;
    justify-content: start;
    margin-left: rem(90);

    .header-page-buttons {
        display: flex;
        justify-content: start;
        gap: rem(10);

        h3 {
            font-size: rem(16);
            font-weight: 400;
            color: var(--text-5);
            cursor: pointer;
            padding: rem(5) rem(8);
            border-radius: rem(50);

            &:hover {
                color: white;
                background-color: rgb(53, 102, 225);
            }
        }
    }

    h2 {
        font-size: rem(24);
        font-weight: 700;
        color: rgb(53, 102, 225);
        margin-right: rem(32);
        cursor: pointer;
    }
}